<template>
  <div id="modal-content">
    <div
      v-if="!isAuthDone"
      id="user-box"
      class="auth-done-false"
      style="width: 100%"
    >
      <div class="user-input signup-email-input">
        <span></span>
        <div class="email-input">
          <input
            :disabled="isAuthOpen"
            type="text"
            name="email"
            placeholder="email@address.com"
            v-model="email"
          />
          <q-icon
            fontSize="large"
            @click="cancelEmail"
            class="email-cancel"
            style="position: absolute; top: 50; left: 70%"
            name="close"
          />
          <div class="email-auth-btns" style="">
            <div v-if="!isAuthOpen" class="email-auth-btns-false">
              <button
                @click.prevent="sendEmail"
                style="background: black"
                class="modal-email-btn"
              >
                
              </button>
            </div>
            <div v-else class="email-auth-btns-true">
              <button class="resend-btn" @click.prevent="sendEmail">
                
              </button>
              <button style="background: black" @click.prevent="changePw">
                
              </button>
            </div>
          </div>
        </div>
      </div>
      <span class="info-text error-message">{{ emailErrorMsg }}</span>
    </div>
    <div v-else class="auth-done-true">
      <span
        > 
        <p>{{ changePassword }}</p>
      </span>
    </div>
  </div>
</template>

<script>
// import Swal from "sweetalert2"
import { ref } from "vue"
import axios from "axios"
import api from "@/api/api"
import { useRouter } from "vue-router"

export default {
  name: "ModalContent",
  setup() {
    const isAuthDone = ref(false)
    const isAuthOpen = ref(false)
    const emailErrorMsg = ref("    ")
    const changePassword = ref("")
    const router = useRouter()

    const email = ref("")

    const cancelEmail = function () {
      email.value = ""
      emailErrorMsg.value = "    "
      isAuthOpen.value = false
    }

    const sendEmail = async function () {
      try {
        const emailRegex = /^[a-zA-Z0-9.^$*?=!:/_-]+@[a-zA-Z]+\.[A-Za-z]{2,4}$/
        if (!emailRegex.test(email.value)) {
          emailErrorMsg.value = "   "
          return
        }
        emailErrorMsg.value = "   "
        let response = await axios({
          url: api.users.sendEmailForPW(),
          method: "POST",
          data: {
            userEmail: email.value,
          },
        })
        if (response.data.statusCode === 200) {
          isAuthOpen.value = true
          emailErrorMsg.value =
            "  .     "
          return
        }
      } catch (error) {
        if (error.response.data.statusCode === 403) {
          emailErrorMsg.value = "    "
        } else if (error.response.data.statusCode === 500) {
          router.push({ name: "errorpage", params: { errorname: 500 } })
        }
      }
    }

    const changePw = async function () {
      try {
        let response = await axios({
          url: api.users.verifyEmailForPW(email.value),
          method: "PUT",
          data: {
            userEmail: email.value,
          },
        })
        if (response.data.statusCode === 200) {
          console.log(response.data)
          changePassword.value = response.data.password
          isAuthDone.value = true
        }
      } catch (error) {
        if (error.response.data.statusCode === 401) {
          emailErrorMsg.value = "   "
        } else if (error.response.data.statusCode === 500) {
          router.push({ name: "errorpage", params: { errorname: 500 } })
        }
      }
    }

    return {
      isAuthDone,
      isAuthOpen,
      emailErrorMsg,
      changePassword,
      email,
      cancelEmail,
      sendEmail,
      changePw,
    }
  },
}
</script>

<style>
@import "../../assets/startpage/modal.css";
</style>
