<template>
  <div id="signup-box">
    <div id="user-box">
      <h5></h5>
      <div class="user-input signup-email-input">
        <span></span>
        <div>
          <div class="email-input">
            <input
              :disabled="isAuthOpen"
              type="text"
              name="email"
              placeholder="email@address.com"
              v-model="credentials.email"
            />
            <q-icon
              fontSize="large"
              @click="cancelEmail"
              class="email-cancel"
              style="position: absolute; top: 50%"
              name="close"
            />
          </div>
          <div class="email-auth-btns">
            <div v-if="!isAuthOpen" class="email-auth-btns-false">
              <button @click="checkEmail"> </button>
            </div>
            <div v-else class="email-auth-btns-true">
              <button class="resend-btn" @click="sendEmail"></button>
            </div>
          </div>
        </div>
      </div>
      <span class="info-text error-message email-error">{{
        emailErrorMsg
      }}</span>
      <div class="signup-password-input">
        <div class="user-input">
          <span></span>
          <input
            type="password"
            name="password"
            v-model="credentials.password"
          />
        </div>
        <div class="user-input" style="margin-right: 0">
          <span> </span>
          <input
            type="password"
            name="passwordConfirm"
            v-model="credentials.passwordConfirm"
          />
        </div>
      </div>
      <span class="info-text pw-error">{{ passwordErrorMsg }}</span>
      <div class="user-input">
        <span></span>
        <input type="text" name="nickname" v-model="credentials.nickname" />
      </div>
      <span class="info-text nickname-error">{{ nicknameErrorMsg }}</span>
      <button @click="checkSignup" class="auth-btn overlay__btn">
        
      </button>
    </div>
    <div id="clause-box">
      <ClauseText />
    </div>
  </div>
</template>

<script>
import Swal from "sweetalert2"
import { ref, reactive } from "vue"
import axios from "axios"
import ClauseText from "./ClauseText.vue"
import api from "@/api/api"
import { useRouter } from "vue-router"

export default {
  name: "SignupInput",
  components: {
    ClauseText,
  },
  // eslint-disable-next-line
  setup(props, context) {
    //    
    const isAuthOpen = ref(false)

    const router = useRouter()
    //  
    //      
    const emailErrorMsg = ref("")
    const passwordErrorMsg = ref(
      ", ,   1  (8~16)"
    )
    const nicknameErrorMsg = ref(" 2~8,  6~24(6~24 byte )")

    //  
    let credentials = reactive({
      email: "",
      password: "",
      passwordConfirm: "",
      nickname: "",
    })

    //   
    const checkEmail = async function () {
      try {
        const emailRegex = /^[a-zA-Z0-9.^$*?=!:/_-]+@[a-zA-Z]+\.[A-Za-z]{2,4}$/
        if (!emailRegex.test(credentials.email)) {
          const emailErrorTxt = document.querySelector(".email-error")
          emailErrorTxt.style.color = "#e31818"
          emailErrorMsg.value = "   "
          return
        }
        let response = await axios({
          url: api.users.duplicateEmail(credentials.email),
          method: "GET",
        })
        if (response.status === 200) {
          isAuthOpen.value = true
          response = sendEmail()
          //  
          return
        }
      } catch (err) {
        if (err.response.data.statusCode === 409) {
          const emailErrorTxt = document.querySelector(".email-error")
          emailErrorTxt.style.color = "#e31818"
          emailErrorMsg.value = "   "
        } else if (err.response.data.statusCode === 500) {
          router.push({ name: "errorpage", params: { errorname: 500 } })
        }
      }
    }

    //  
    const cancelEmail = function () {
      const emailErrorTxt = document.querySelector(".email-error")
      emailErrorTxt.style.color = "#625eef"
      credentials.email = ""
      emailErrorMsg.value = ""
      isAuthOpen.value = false
    }

    //   &  
    const sendEmail = async function () {
      try {
        const emailErrorTxt = document.querySelector(".email-error")
        emailErrorTxt.style.color = "#625eef"
        emailErrorMsg.value = "    "
        const response = await axios({
          url: api.users.sendEmail(),
          method: "POST",
          data: {
            userEmail: credentials.email,
          },
        })
        if (response.data.statusCode === 200) {
          emailErrorMsg.value = "  "
        }
      } catch (err) {
        if (err.response.data.statusCode === 400) {
          emailErrorMsg.value = "    "
        } else if (err.response.data.statusCode === 500) {
          router.push({ name: "errorpage", params: { errorname: 500 } })
        }
      }
    }

    //  
    const checkSignup = async function () {
      try {
        let errorFlag = true
        //  
        const passwordRegex =
          /^(?=.*[A-Za-z])(?=.*\d)(?=.*[@$!%*#?&])[A-Za-z\d@$!<>{}|\\*[\]/%*#()?&]{8,16}$/
        //   
        //    
        if (isAuthOpen.value === true) {
          // eslint-disable-next-line
          const response = await axios.get(
            api.users.verifyEmail(credentials.email)
          )
        }
        // 
        if (!credentials.password || !credentials.passwordConfirm) {
          const pwErrorTxt = document.querySelector(".pw-error")
          pwErrorTxt.style.color = "#e31818"
          passwordErrorMsg.value = " "
          setTimeout(() => {
            pwErrorTxt.style.color = "#625eef"
            passwordErrorMsg.value =
              ", ,   1  (8~16)"
          }, 3000)
          errorFlag = false
        } else if (
          errorFlag === true &&
          !passwordRegex.test(credentials.password)
        ) {
          const pwErrorTxt = document.querySelector(".pw-error")
          pwErrorTxt.style.color = "#e31818"
          passwordErrorMsg.value = "  ."
          setTimeout(() => {
            pwErrorTxt.style.color = "#625eef"
            passwordErrorMsg.value =
              ", ,   1  (8~16)"
          }, 3000)
          errorFlag = false
        } else if (
          errorFlag === true &&
          credentials.password !== credentials.passwordConfirm
        ) {
          const pwErrorTxt = document.querySelector(".pw-error")
          pwErrorTxt.style.color = "#e31818"
          passwordErrorMsg.value = "   "
          setTimeout(() => {
            pwErrorTxt.style.color = "#625eef"
            passwordErrorMsg.value =
              ", ,   1  (8~16)"
          }, 3000)
          errorFlag = false
        }
        // 
        if (!credentials.nickname) {
          const nicknameErrorTxt = document.querySelector(".nickname-error")
          nicknameErrorTxt.style.color = "#e31818"
          nicknameErrorMsg.value = " "
          errorFlag = false
        } else if (
          errorFlag === true &&
          (!(
            6 <=
            credentials.nickname.replace(
              /[\0-\x7f]|([0-\u07ff]|(.))/g,
              "$&$1$2"
            ).length
          ) ||
            !(
              credentials.nickname.replace(
                /[\0-\x7f]|([0-\u07ff]|(.))/g,
                "$&$1$2"
              ).length <= 24
            ) ||
            !/^[-|-|a-z|A-Z|]+$/.test(credentials.nickname))
        ) {
          const nicknameErrorTxt = document.querySelector(".nickname-error")
          nicknameErrorTxt.style.color = "#e31818"
          nicknameErrorMsg.value = "  ."
          setTimeout(() => {
            nicknameErrorTxt.style.color = "#625eef"
            nicknameErrorMsg.value = " 2~8,  6~24(6~24 byte )"
          }, 3000)
          errorFlag = false
        }
        //  false   return
        if (errorFlag === false) {
          return
        }
        const response = await axios({
          url: api.users.signup(),
          method: "POST",
          data: {
            userEmail: credentials.email,
            userPassword: credentials.password,
            userNickname: credentials.nickname,
          },
        })
        if (response.status === 201) {
          Swal.fire({
            position: "top-end",
            icon: "success",
            title: " ",
            showConfirmButton: false,
            timer: 1500,
          })
          //    
          context.emit("signup-done")
          return
        }
      } catch (err) {
        if (err.response.status === 401) {
          emailErrorMsg.value = "   "
        } else if (err.response.status === 400) {
          emailErrorMsg.value = "  "
        } else {
          router.push({ name: "errorpage", params: { errorname: 500 } })
        }
      }
    }

    return {
      isAuthOpen,
      emailErrorMsg,
      passwordErrorMsg,
      nicknameErrorMsg,
      credentials,
      sendEmail,
      checkEmail,
      checkSignup,
      cancelEmail,
    }
  },
}
</script>

