<template>
  <form @submit.prevent="login">
    <div id="user-box">
      <h5></h5>
      <div class="user-input">
        <span></span>
        <input
          type="text"
          name="email"
          placeholder="email@address.com"
          v-model="credentials.email"
        />
      </div>
      <div class="user-input">
        <span></span>
        <input type="password" name="email" v-model="credentials.password" />
      </div>
      <span class="error-message">{{ loginErrorMsg }}</span>
      <button class="auth-btn overlay__btn"></button>
      <span @click="kakaoLogin" class="kakao-btn">
        <p> </p>
        <img src="../../assets/kakao-logo.png" />
      </span>
      <span @click="openModal" class="forget-pw-text"
        > ?</span
      >
    </div>
    <div>
      <TheModal v-if="modal" @close="openModal">
        <ModalContent />
      </TheModal>
    </div>
  </form>
</template>

<script>
import { ref, reactive } from "vue"
import { useStore } from "vuex"
import router from "@/router"
import axios from "axios"
import TheModal from "./TheModal.vue"
import ModalContent from "./ModalContent.vue"
import api from "@/api/api"
export default {
  name: "LoginInput",
  components: {
    TheModal,
    ModalContent,
  },
  setup() {
    const kakaoLogin = function () {
      window.location.href = `https://kauth.kakao.com/oauth/authorize?response_type=code&client_id=${
        process.env.VUE_APP_KAKAO_JAVASCRIPT_API_KEY
      }&redirect_uri=${api.users.kakaoLogin()}`
    }

    //  
    const store = useStore()
    // 
    const modal = ref(false)
    const openModal = function () {
      modal.value = !modal.value
    }

    //   
    const loginErrorMsg = ref("")

    //   
    const credentials = reactive({
      email: "",
      password: "",
    })

    //  
    const login = async function () {
      try {
        if (credentials.email && credentials.password) {
          const response = await axios({
            url: api.users.login(),
            method: "POST",
            data: {
              userEmail: credentials.email,
              userPassword: credentials.password,
            },
          })
          if (response.status === 200) {
            const token = response.data.accessToken
            store.dispatch("saveToken", token)
            store.dispatch("fetchMe")
            //    
            router.push({ name: "mainpage" })
          }
        } else {
          const errorTxt = document.querySelector(".error-message")
          errorTxt.style.color = "#e31818"
          loginErrorMsg.value = "  "
          return
        }
      } catch (err) {
        const errorTxt = document.querySelector(".error-message")
        errorTxt.style.color = "#e31818"
        loginErrorMsg.value = "    "
        return
      }
    }

    return {
      modal,
      loginErrorMsg,
      openModal,
      credentials,
      login,
      kakaoLogin,
    }
  },
}
</script>

