<template>
  <div class="top-bar">
    <div>
      <button
        v-if="isGameMode === 2"
        class="manual-button"
        @click="liarGameManual"
      >
        <span></span>
      </button>
      <button
        v-if="isGameMode === 3"
        class="manual-button"
        @click="callMyNameManual"
      >
        <span></span>
      </button>
    </div>
    <div v-if="isGameMode === 2">
      <!--    -->
      <div v-if="isLiar === 1">
        <span class="notify-span"> Liar</span>
      </div>
      <!--     -->
      <div v-if="isLiar === 2">
        <span class="notify-span">: {{ suggestion }}</span>
      </div>
    </div>
    <div v-if="isGameMode === 3"></div>
    <div>
      <button v-if="isHost" class="end-button" @click="$emit('quitGame')">
        <div>
          <i class="fa-solid fa-person-running fa-xl"></i>
        </div>
        <div>
          <span> </span>
        </div>
      </button>
    </div>
  </div>
</template>
<script setup>
import store from "@/store"
import { defineProps, watchEffect, ref } from "vue"
import Swal from "sweetalert2"
// eslint-disable-next-line

const props = defineProps({
  isHost: Boolean,
  isGameMode: Number,
  suggestion: String,
  isLiar: Boolean,
})
const gameSet = ref(store.getters.getLiarGameSet)
const isLiar = ref(JSON.parse(props.isLiar))
watchEffect(() => {
  console.log("")
  gameSet.value = store.getters.getLiarGameSet
  console.log(
    "%c gameSet   (from TopBar.vue) ",
    "color: white;background: pink",
    props.suggestion,
    props.isLiar
  )
})

//  
const liarGameManual = () => {
  Swal.fire({
    imageHeight: 300,
    imageAlt: "game image",
    title: " ",
    text: " ?",
    showCancelButton: true,
    showDenyButton: true,
    denyButtonText: "",
    confirmButtonText: " ",
    cancelButtonText: "",
  }).then((result) => {
    if (result.isConfirmed === true) {
      Swal.fire({
        title: "  ",
        html: "      .<br>      <br>     .<br>   <br>     ?<br><br>   <br>      <br>   !",
        confirmButtonText: "",
        width: 600,
      })
    } else if (result.isDenied === true) {
      Swal.fire({
        title: " ",
        html: "    ,    .<br>        <br>        .<br>     !",
        showCancelButton: true,
        confirmButtonText: " ",
        confirmButtonColor: "#dc3741",
        cancelButtonText: "",
        width: 600,
      }).then((result) => {
        if (result.isConfirmed === true) {
          Swal.fire({
            title: " ",
            html: " !<br>    !<br>   <br>    .<br>     <br> !",
            confirmButtonText: "",
            confirmButtonColor: "#dc3741",
            width: 600,
          })
        }
      })
    }
  })
}

//    
const callMyNameManual = () => {
  Swal.fire({
    title: "  !",
    html: "    <br>   .<br><br>  .<br>      .<br><br>      <br>  !",
    confirmButtonText: "",
  })
}
</script>
<style scoped>
@import url("../../../../assets/roompage/topbar.css");
</style>
