import Swal from "sweetalert2"
import { createRouter, createWebHistory } from "vue-router"
import StartPage from "../views/StartPage.vue"
import MainPage from "@/views/MainPage.vue"
import ErrorPage from "@/views/ErrorPage.vue"
import RoomPage from "@/views/RoomPage.vue"
import LobbyPage from "@/views/LobbyPage.vue"

import KakaoLoginPage from "@/views/KakaoLoginPage.vue"
// eslint-disable-next-line
import store from "@/store"

const routes = [
  {
    path: "/",
    name: "startpage",
    component: StartPage,
  },
  {
    path: "/lounge",
    name: "mainpage",
    component: MainPage,
  },
  {
    path: "/room/:roomId",
    name: "roompage",
    component: RoomPage,
  },
  {
    path: "/room/lobby",
    name: "lobbypage",
    component: LobbyPage,
  },
  //        
  {
    path: "/error/:errorname",
    name: "errorpage",
    component: ErrorPage,
  },
  {
    path: "/login/kakao",
    name: "kakaologin",
    component: KakaoLoginPage,
  },
  {
    path: "/:pathMatch(.*)*",
    redirect: "/error/404",
  },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
})

router.beforeEach((to, from, next) => {
  //   navguard
  const { isLoggedin } = store.getters
  //   

  const authRequiredPages = ["roompage", "mainpage", "lobbypage"]

  //   
  if (isLoggedin) {
    if (to.name === "startpage" || to.name === "kakaologin") {
      next({ name: "mainpage" })
    } else {
      next()
    }
    //    
  } else if (!isLoggedin) {
    if (authRequiredPages.includes(to.name)) {
      Swal.fire({
        icon: "warning",
        text: "  !",
      })
      next({ name: "startpage" })
    } else {
      next()
    }
  }
})

export default router
